import React from 'react';
import styled from 'styled-components';
import { Container, Row, Col, Nav } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { fab, fas } from '@fortawesome/free-brands-svg-icons'
import { library } from '@fortawesome/fontawesome-svg-core'

import { faTwitter } from '@fortawesome/free-brands-svg-icons/faTwitter'
import { faGithub } from '@fortawesome/free-brands-svg-icons/faGithub'
import { faEnvelope } from '@fortawesome/free-solid-svg-icons/faEnvelope'
import { faMedium } from '@fortawesome/free-brands-svg-icons/faMedium'

const Body = styled.div`
  color: #61dbfb;;
	background: #fff;
	min-width: 1000px;
`

const Ol = styled.ol`
  position: relative;
  display: block;
  margin: 80px;
  height: 4px;
  background: #867e7e;

  &::before,

  &::after {
  content: "";
  position: absolute;
  top: -8px;
  display: block;
  width: 0;
  height: 0;
  border-radius: 10px;
  border: 10px solid #61dbfb;
  }

  &::before {
  left: -5px;
  }

  &::after {
  right: -10px;
  border: 10px solid transparent;
  border-right: 0;
  border-left: 20px solid #61dbfb;
  border-radius: 10px;
  }
`

const Li = styled.li`
  position: relative;
  display: inline-block;
  float: left;
  width: 150px;
  font: bold 14px arial;
  height: 50px;
}
`
const Diplome = styled.p`
  position: absolute;
  top: -47px;
  left: 36%;
  color: #000000;
`
const Description = styled.p`
  position: relative;
  top: 16px;
  left: 36%;
  color: #000000;
  font-family: 'proxima-nova', futura-pt space;
  font-size: 12px;
  display: block;
  font-color: rgb(0, 0, 0);
  text-align: left;
  vertical-align: baseline;
  word-wrap: break-word;
`
const Point = styled.span`
  content: "";
  top: -4px;
  left: 43%;
  display: block;
  width: 6px;
  height: 6px;
  border: 4px solid #61dbfb;
  border-radius: 10px;
  background: #61dbfb;
  position: absolute;
`


const EducationLine = () => (
  <Container>
    <Body>
      <Ol>
        <Li>
          <Diplome>2018</Diplome>
          <Point></Point>
          <Description>Student of Software Development - Self-taught</Description>
        </Li>
        <Li>
          <Diplome>2017 - 2018</Diplome>
          <Point></Point>
          <Description>Master of Science - MS in Business Administration and Management - Federal University of Itajubá, Minas Gerais state, Brazil.</Description>
        </Li>
        <Li>
          <Diplome>2018</Diplome>
          <Point></Point>
          <Description>Student of Software Development - Self-taught</Description>
        </Li>
        <Li />
        <Li>
          <Diplome>2018</Diplome>
          <Point></Point>
          <Description>Student of Software Development - Self-taught</Description>
        </Li>
      </Ol>
    </Body>
  </Container>
);


export default EducationLine;



